<template>
  <div>
    <v-header>
    </v-header>

    <v-content :type="'none'">
      <div class="detail-box">
        <div class="amount-way">
          <span class="left">{{detail.direction==0?'入账金额':'出账金额'}}</span>
          <span :class="['right mg-r10 amount',detail.direction==0?'income':'pay']">{{detail.direction==0?'+':'-'}}{{detail.amount | currency ''}}</span>
        </div>
        <div class="info color-9">
          <div>
            <span class="left">类型</span>
            <span class="right mg-r10">{{detail.typeName}}</span>
          </div>
          <div>
            <span class="left">时间</span>
            <span class="right mg-r10">{{detail.time}}</span>
          </div>
          <div>
            <span class="left">交易单号</span>
            <span class="right mg-r10">{{detail.sn}}</span>
          </div>
          <div>
            <span class="left">账户余额</span>
            <span class="right mg-r10">{{detail.balance | currency ''}}</span>
          </div>
        </div>
        <div class="remark">
          <span class="left">备注</span>
          <span class="right mg-r10">{{detail.remark}}</span>
        </div>
      </div>
    </v-content>
  </div>
</template>
<script>
  import VHeader from '../../components/Header'
  import VContent from '../../components/Content'
  import {lt, url} from '../../const'
  import {toast} from '../../assets/js/toast'
  import $ from 'jquery'

  export default{
    route: {
      data({from, to}){
        let _url = url.fundDetailShow;
        if (!to.params.id&&!to.query.taskId) {
          window.location.href = "#/wallet/capitalLogs";
          return;
        }
        let id = to.params.id;
        if(to.query.taskId){
          id = to.query.taskId;
          _url = url.checkPayment;
        }
        return this.$http.get(_url, {id: id}).then(
                ({data:{data, success, resultMsg}}) => {
                  if (success) {
                    this.detail = data;
                  } else {
                    toast.alert(resultMsg);
                  }
                });
      }
    },
    data(){
      return {
        detail: {}
      }
    },
    components: {VHeader, VContent}
  }
</script>
<style scoped>
  .detail-box {
    border-top: .01rem solid #e5e5e5;
    border-bottom: .01rem solid #e5e5e5;
    background-color: #fff;
  }

  .detail-box > div {
    margin-left: .1rem;
    border-bottom: .01rem solid #e5e5e5;
  }

  .detail-box .amount-way {
    font-size: .16rem;
    color: #333;
    height: .45rem;
    line-height: .45rem;
  }

  .detail-box .info {
    padding-top: .05rem;
    padding-bottom: .05rem;
  }

  .detail-box .info div {
    height: .25rem;
    line-height: .25rem;
  }

  .detail-box .remark {
    height: .45rem;
    line-height: .45rem;
    color: #999;
    border-bottom: none;
  }

  .amount.income {
    color: #ffb600;
  }

  .amount.pay {
    color: #00cda9;
  }
</style>




